    --header-height: 50px;
    --sidebar-width: 18rem;

    --header-bg-color: #eff1f6;
    --sidebar-bg-color: #eff1f6;
    --sidebar-button-color: white;
    --body-bg-color: white;
    --box-shadow-color: rgb(225, 225, 225);

    --modal-form-color: white;
    --modal-btn-affirm: #d8f6d5;
    --modal-border-affirm: #aed3a2;
    --modal-btn-deny: #f7d5d5;
    --modal-border-deny: #e9baba;
    --modal-btn-neutral: #eff1f6;
    --modal-border-neutral: #d7d9df;

    --low-priority-color: rgb(32, 184, 118);
    --medium-priority-color: rgb(255, 228, 74);
    --high-priority-color: rgb(240, 145, 132);
    --date-color: #6091b7;

    --light-gray: rgb(225, 225, 225);
    --med-gray: rgb(154, 154, 154);
    --font-color: #505050;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 600px;
    position: relative;
    background-color: var(--body-bg-color);
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: .05em;
    color: var(--font-color);
}

button {
    background: none;
    padding: 7px 15px;
    font-size: 1rem;
    border-radius: 5px;
    border: 1px solid;
    cursor: pointer;
    color: var(--font-color);
    box-shadow: 1px 1px 1px rgb(233, 233, 233);
}

button[type="submit"] {
    background-color: var(--modal-btn-affirm);
    border: 1px solid var(--modal-border-affirm);
}

.cancel-btn {
    background-color: var(--modal-btn-neutral);
    border: 1px solid var(--modal-border-neutral);
}

input[type="checkbox"], label[class="expand-label"] {
    cursor: pointer;
}

/*****************************
* Header
******************************/
header {
    padding: 0 20px;
    width: 100%;
    height: var(--header-height);
    display: none;
    align-items: center;
    background-color: var(--header-bg-color);
    box-shadow: 0px 2px 10px var(--box-shadow-color);
}

#hamburger{ 
    display: none;
    border: none;
    flex-direction: column;
    gap: 5px;
    padding: 0;
}  

#hamburger span {
    width: 30px;
    height: 2px;
    background-color: black;
    border-radius: 5px;
    transition: transform 500ms, opacity 500ms;
}

#hamburger.active span:first-child {
    transform: rotate(-45deg) translate(-5px, 5px);
}

#hamburger.active span:nth-child(2) {
    opacity: 0;
}

#hamburger.active span:last-child {
    transform: rotate(45deg) translate(-5px, -5px);
}

/*****************************
* Navigation sidebar
******************************/
#sidebar {
    height: 100%;
    width: var(--sidebar-width);
    position: absolute;
    padding: 20px;
    display: flex;
    flex-direction: column;
    background-color: var(--sidebar-bg-color);
    box-shadow: 2px 0px 10px var(--box-shadow-color);
}

#user {
    font-weight: bold;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 15px;
    font-size: .9rem;
    margin: -20px -20px 50px -20px;
    padding: 10px 10px 10px 20px;
    background-color: var(--sidebar-bg-color-dark);
}

#user a {
    height: 40px;
}

#profile-pic {
    width: 40px;
    height: 40px;
    padding: 3px;
    border: 1px solid rgb(147, 147, 147);
    border-radius: 50%;
    transition: box-shadow 500ms;
}

#profile-pic:hover {
    box-shadow: 0 0 10px 2px blue;
}

nav {
    flex: 1;
    display: flex;
    flex-direction: column;
}

nav h2 {
    border-bottom: 2px solid var(--light-gray);
    margin-bottom: 15px;
    padding-bottom: 10px;
}

nav > button, #project-list button {
    display: flex;
    align-items: center;
    gap: 15px;
    border: none;
    border-radius: 0;
    text-align: left;
    padding: 15px 0px 15px 10px;
    box-shadow: none;
}

#project-list button img, .project-title-wrapper {
    pointer-events: none;
}

.project-title-wrapper {
    overflow: hidden;
    text-wrap: nowrap;
    text-overflow: ellipsis;
}

nav button:hover {
    background-color: var(--sidebar-button-color);
}

nav h2:not(:first-child) {
    margin-top: 30px;
}

#project-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#create-project  {
    margin-left: auto;
}

#create-project, .add-task {
    border: 1px solid var(--modal-border-affirm);
    background-color: var(--modal-btn-affirm);
    padding: 5px 14px;
    border-radius: 15px;
    font-size: .8rem; 
}

#project-list {
    flex: 1 1 0;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
}

/**************************
* Main content
**************************/
#main {
    min-height: 600px;
    width: calc(100% - var(--sidebar-width));
    margin-left: 18rem;
    padding: 20px 5%;
    overflow: hidden;
}

.view-header {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 50px;
    font-size: 1.8rem;
    gap: 10px;
    letter-spacing: .05em;
}

.view-header span {
    font-size: 1rem;
    color: var(--date-color);
}

.task-component {
    width: 100%;
    font-size: .9rem;
    max-height: 35px;
    overflow: hidden;
    transition: max-height 500ms;
    border-bottom: 1px solid var(--light-gray);
    margin-bottom: 15px;
    display: grid;
    grid-template-columns: 3px 13px 1fr repeat(3, min-content);
    grid-template-rows: min-content 1fr;
    align-items: center;
    gap: 15px;
}

.task-component p:first-of-type {
    text-wrap: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.priority-tag {
    width: 100%;
    height: 30px;
}

.priority-tag.low {
    background-color: var(--low-priority-color);
}

.priority-tag.medium {
    background-color: var(--medium-priority-color);
}

.priority-tag.high {
    background-color: var(--high-priority-color);
}

.task-due {
    color: var(--date-color);
    font-size: .8rem;
}

.task-btn,
.task-img {
    width: 19px;
    height: 19px;
    box-shadow: none;
}

.task-btn,
.expand-label {
    padding: 0;
    border: none;
    margin: 0 3px;
}

.expand-label {
    width: min-content;
    transition: transform 300ms;
}

.expand-task {
    display: none;
}

.expand-task:checked + .expand-label {
    transform: rotate(-180deg);
}

.complete-label {
    display: none;
}

.expanded-task-content {
    grid-column: 2 / -1;
    transition: visibility 500ms;
    visibility: hidden;
}

.expand-task:checked ~ .expanded-task-content {
    visibility: visible;
}

.expanded-task-content span {
    font-weight: bold;
}

.expanded-task-content .task-parent, .expanded-task-content .task-title  {
    margin-bottom: 15px;
}

.expanded-task-content .task-description {
    line-height: 1.5em;
    padding-bottom: 15px;
}

/**************************
* Project views
**************************/
.project-btns {
    display: flex;
    justify-content: right;
}

.add-task {
    margin-bottom: 20px;
    margin-right: -5px;
}

/*****************************
* Modal
******************************/
.modal-overlay {
    background-color: rgba(128, 128, 128, 0.415);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    background-color: white;
    border-radius: 5px;
    width: 400px;
    margin: 10px;
}

.modal-header {

    padding: 20px;
    font-size: 1.7rem;
    background-color: var(--header-bg-color);
    border-radius: 5px 4px 0 0;
}

.modal form {
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.modal form input,
.modal form select {
    padding: .5em 5px;
    margin-top: 5px;
    margin-bottom: 20px;
    border: 1px solid gray;
    border-radius: 5px;
}

.modal form input:focus {
    outline: none;
    border: 1px solid blue;
}

.modal-btns {
    border-top: 1px solid gray;
    padding-top: 20px;
    display: flex;
    justify-content: right;
    gap: 20px;
}

#del-task-btns {
    border: none;
}

#edit-task-name.invalid,
#edit-task-due.invalid,
.modal form input.invalid {
    border: 1px solid red;
}

#edit-task-btns {
    justify-content: space-between;
}

#delete-task, #del-task-submit {
    background-color: var(--modal-btn-deny);
    border: 1px solid var(--modal-border-deny);
}

#cancel-edit-task {
    margin-right: 20px;
}

/**************************
* Media Queries
**************************/
/* 500px and below */
@media only screen and (max-width: 500px) {
    nav h2 {
        font-size: 1.3rem;
    }
    nav > button, #project-list button {
        font-size: .9rem;
    }

    #create-project, .add-task {
        font-size: .7rem;
    }

    #view-header {
        font-size: 1.6rem;
    }

    .task-component {
        font-size: .8rem;
    }

    .task-due {
        font-size: .7rem;
    }
}

/* 800px and below */
@media only screen and (max-width: 800px) {
    #sidebar {
        transform: translateX(calc(-1 * var(--sidebar-width)));
        transition: margin-left 500ms, box-shadow 500ms;
        height: calc(100% - var(--header-height));
        box-shadow: none;
    }


    #sidebar.expand {
        margin-left: 18rem;
        box-shadow: 2px 0px 10px var(--box-shadow-color);
        transition: margin-left 500ms;
    }

    #main {
        margin-left: 0;
        width: 100%;
        min-height: calc(600px - var(--header-height));
        height: calc(100vh - var(--header-height));
    }

    header {
        display: flex;
    }

    #hamburger {
        display: flex;
    }
