--header-height: 50px;\n    --sidebar-width: 18rem;\n\n    --header-bg-color: #eff1f6;\n    --sidebar-bg-color: #eff1f6;\n    --sidebar-button-color: white;\n    --body-bg-color: white;\n    --box-shadow-color: rgb(225, 225, 225);\n\n    --modal-form-color: white;\n    --modal-btn-affirm: #d8f6d5;\n    --modal-border-affirm: #aed3a2;\n    --modal-btn-deny: #f7d5d5;\n    --modal-border-deny: #e9baba;\n    --modal-btn-neutral: #eff1f6;\n    --modal-border-neutral: #d7d9df;\n\n    --low-priority-color: rgb(32, 184, 118);\n    --medium-priority-color: rgb(255, 228, 74);\n    --high-priority-color: rgb(240, 145, 132);\n    --date-color: #6091b7;\n\n    --light-gray: rgb(225, 225, 225);\n    --med-gray: rgb(154, 154, 154);\n    --font-color: #505050;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    min-height: 600px;\n    position: relative;\n    background-color: var(--body-bg-color);\n    font-family: Arial, Helvetica, sans-serif;\n    letter-spacing: .05em;\n    color: var(--font-color);\n}\n\nbutton {\n    background: none;\n    padding: 7px 15px;\n    font-size: 1rem;\n    border-radius: 5px;\n    border: 1px solid;\n    cursor: pointer;\n    color: var(--font-color);\n    box-shadow: 1px 1px 1px rgb(233, 233, 233);\n}\n\nbutton[type=\"submit\"] {\n    background-color: var(--modal-btn-affirm);\n    border: 1px solid var(--modal-border-affirm);\n}\n\n.cancel-btn {\n    background-color: var(--modal-btn-neutral);\n    border: 1px solid var(--modal-border-neutral);\n}\n\ninput[type=\"checkbox\"], label[class=\"expand-label\"] {\n    cursor: pointer;\n}\n\n/*****************************\n* Header\n******************************/\nheader {\n    padding: 0 20px;\n    width: 100%;\n    height: var(--header-height);\n    display: none;\n    align-items: center;\n    background-color: var(--header-bg-color);\n    box-shadow: 0px 2px 10px var(--box-shadow-color);\n}\n\n#hamburger{ \n    display: none;\n    border: none;\n    flex-direction: column;\n    gap: 5px;\n    padding: 0;\n}  \n\n#hamburger span {\n    width: 30px;\n    height: 2px;\n    background-color: black;\n    border-radius: 5px;\n    transition: transform 500ms, opacity 500ms;\n}\n\n#hamburger.active span:first-child {\n    transform: rotate(-45deg) translate(-5px, 5px);\n}\n\n#hamburger.active span:nth-child(2) {\n    opacity: 0;\n}\n\n#hamburger.active span:last-child {\n    transform: rotate(45deg) translate(-5px, -5px);\n}\n\n/*****************************\n* Navigation sidebar\n******************************/\n#sidebar {\n    height: 100%;\n    width: var(--sidebar-width);\n    position: absolute;\n    padding: 20px;\n    display: flex;\n    flex-direction: column;\n    background-color: var(--sidebar-bg-color);\n    box-shadow: 2px 0px 10px var(--box-shadow-color);\n}\n\n#user {\n    font-weight: bold;\n    display: flex;\n    justify-content: left;\n    align-items: center;\n    gap: 15px;\n    font-size: .9rem;\n    margin: -20px -20px 50px -20px;\n    padding: 10px 10px 10px 20px;\n    background-color: var(--sidebar-bg-color-dark);\n}\n\n#user a {\n    height: 40px;\n}\n\n#profile-pic {\n    width: 40px;\n    height: 40px;\n    padding: 3px;\n    border: 1px solid rgb(147, 147, 147);\n    border-radius: 50%;\n    transition: box-shadow 500ms;\n}\n\n#profile-pic:hover {\n    box-shadow: 0 0 10px 2px blue;\n}\n\nnav {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n}\n\nnav h2 {\n    border-bottom: 2px solid var(--light-gray);\n    margin-bottom: 15px;\n    padding-bottom: 10px;\n}\n\nnav > button, #project-list button {\n    display: flex;\n    align-items: center;\n    gap: 15px;\n    border: none;\n    border-radius: 0;\n    text-align: left;\n    padding: 15px 0px 15px 10px;\n    box-shadow: none;\n}\n\n#project-list button img, .project-title-wrapper {\n    pointer-events: none;\n}\n\n.project-title-wrapper {\n    overflow: hidden;\n    text-wrap: nowrap;\n    text-overflow: ellipsis;\n}\n\nnav button:hover {\n    background-color: var(--sidebar-button-color);\n}\n\nnav h2:not(:first-child) {\n    margin-top: 30px;\n}\n\n#project-list-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n#create-project  {\n    margin-left: auto;\n}\n\n#create-project, .add-task {\n    border: 1px solid var(--modal-border-affirm);\n    background-color: var(--modal-btn-affirm);\n    padding: 5px 14px;\n    border-radius: 15px;\n    font-size: .8rem; \n}\n\n#project-list {\n    flex: 1 1 0;\n    overflow-y: scroll;\n    display: flex;\n    flex-direction: column;\n}\n\n/**************************\n* Main content\n**************************/\n#main {\n    min-height: 600px;\n    width: calc(100% - var(--sidebar-width));\n    margin-left: 18rem;\n    padding: 20px 5%;\n    overflow: hidden;\n}\n\n.view-header {\n    display: flex;\n    justify-content: center;\n    text-align: center;\n    align-items: center;\n    margin-top: 10px;\n    margin-bottom: 50px;\n    font-size: 1.8rem;\n    gap: 10px;\n    letter-spacing: .05em;\n}\n\n.view-header span {\n    font-size: 1rem;\n    color: var(--date-color);\n}\n\n.task-component {\n    width: 100%;\n    font-size: .9rem;\n    max-height: 35px;\n    overflow: hidden;\n    transition: max-height 500ms;\n    border-bottom: 1px solid var(--light-gray);\n    margin-bottom: 15px;\n    display: grid;\n    grid-template-columns: 3px 13px 1fr repeat(3, min-content);\n    grid-template-rows: min-content 1fr;\n    align-items: center;\n    gap: 15px;\n}\n\n.task-component p:first-of-type {\n    text-wrap: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.priority-tag {\n    width: 100%;\n    height: 30px;\n}\n\n.priority-tag.low {\n    background-color: var(--low-priority-color);\n}\n\n.priority-tag.medium {\n    background-color: var(--medium-priority-color);\n}\n\n.priority-tag.high {\n    background-color: var(--high-priority-color);\n}\n\n.task-due {\n    color: var(--date-color);\n    font-size: .8rem;\n}\n\n.task-btn,\n.task-img {\n    width: 19px;\n    height: 19px;\n    box-shadow: none;\n}\n\n.task-btn,\n.expand-label {\n    padding: 0;\n    border: none;\n    margin: 0 3px;\n}\n\n.expand-label {\n    width: min-content;\n    transition: transform 300ms;\n}\n\n.expand-task {\n    display: none;\n}\n\n.expand-task:checked + .expand-label {\n    transform: rotate(-180deg);\n}\n\n.complete-label {\n    display: none;\n}\n\n.expanded-task-content {\n    grid-column: 2 / -1;\n    transition: visibility 500ms;\n    visibility: hidden;\n}\n\n.expand-task:checked ~ .expanded-task-content {\n    visibility: visible;\n}\n\n.expanded-task-content span {\n    font-weight: bold;\n}\n\n.expanded-task-content .task-parent, .expanded-task-content .task-title  {\n    margin-bottom: 15px;\n}\n\n.expanded-task-content .task-description {\n    line-height: 1.5em;\n    padding-bottom: 15px;\n}\n\n/**************************\n* Project views\n**************************/\n.project-btns {\n    display: flex;\n    justify-content: right;\n}\n\n.add-task {\n    margin-bottom: 20px;\n    margin-right: -5px;\n}\n\n/*****************************\n* Modal\n******************************/\n.modal-overlay {\n    background-color: rgba(128, 128, 128, 0.415);\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    visibility: hidden;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.modal {\n    background-color: white;\n    border-radius: 5px;\n    width: 400px;\n    margin: 10px;\n}\n\n.modal-header {\n\n    padding: 20px;\n    font-size: 1.7rem;\n    background-color: var(--header-bg-color);\n    border-radius: 5px 4px 0 0;\n}\n\n.modal form {\n    padding: 20px;\n    display: flex;\n    flex-direction: column;\n}\n\n.modal form input,\n.modal form select {\n    padding: .5em 5px;\n    margin-top: 5px;\n    margin-bottom: 20px;\n    border: 1px solid gray;\n    border-radius: 5px;\n}\n\n.modal form input:focus {\n    outline: none;\n    border: 1px solid blue;\n}\n\n.modal-btns {\n    border-top: 1px solid gray;\n    padding-top: 20px;\n    display: flex;\n    justify-content: right;\n    gap: 20px;\n}\n\n#del-task-btns {\n    border: none;\n}\n\n#edit-task-name.invalid,\n#edit-task-due.invalid,\n.modal form input.invalid {\n    border: 1px solid red;\n}\n\n#edit-task-btns {\n    justify-content: space-between;\n}\n\n#delete-task, #del-task-submit {\n    background-color: var(--modal-btn-deny);\n    border: 1px solid var(--modal-border-deny);\n}\n\n#cancel-edit-task {\n    margin-right: 20px;\n}\n\n/**************************\n* Media Queries\n**************************/\n/* 500px and below */\n@media only screen and (max-width: 500px) {\n    nav h2 {\n        font-size: 1.3rem;\n    }\n    nav > button, #project-list button {\n        font-size: .9rem;\n    }\n\n    #create-project, .add-task {\n        font-size: .7rem;\n    }\n\n    #view-header {\n        font-size: 1.6rem;\n    }\n\n    .task-component {\n        font-size: .8rem;\n    }\n\n    .task-due {\n        font-size: .7rem;\n    }\n}\n\n/* 800px and below */\n@media only screen and (max-width: 800px) {\n    #sidebar {\n        transform: translateX(calc(-1 * var(--sidebar-width)));\n        transition: margin-left 500ms, box-shadow 500ms;\n        height: calc(100% - var(--header-height));\n        box-shadow: none;\n    }\n\n\n    #sidebar.expand {\n        margin-left: 18rem;\n        box-shadow: 2px 0px 10px var(--box-shadow-color);\n        transition: margin-left 500ms;\n    }\n\n    #main {\n        margin-left: 0;\n        width: 100%;\n        min-height: calc(600px - var(--header-height));\n        height: calc(100vh - var(--header-height));\n    }\n\n    header {\n        display: flex;\n    }\n\n    #hamburger {\n        display: flex;\n    }\n}"],"sourceRoot":""}]);
